.r-input-text {
  border: none;
  border-bottom: 1px solid gray;
  outline: none;
  padding: 5px 10px;
}

.r-title {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 3rem;
}

.r-title-2 {
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 2.4rem;
}

.r-btn-outline {
  margin: 5px;
  display: inline;
  cursor: pointer;
  border: 1px solid currentColor;
  padding: 0.5rem 1rem;
  border-radius: 6px;
}
